<template>

<div id="view-employee">
    <h4><router-link to="/"><i class="material-icons inline-icon">arrow_back</i>
    </router-link> Employee details</h4>

    <hr>
    <ul class="colllection">
        <li><h5>{{ name }}</h5></li>
        <li class="collection-item">Employee ID: {{ employee_id }}</li>
        <li class="collection-item">Department: {{ dept }}</li>
        <li class="collection-item">Position: {{ position }}</li>
        <li class="collection-item">Type: {{ type }}</li>

    </ul>
    <hr>
    <br>
    
    <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn green">Edit</router-link>
    <button @click="deleteEmployee" to="/" class="btn red">Delete</button>  
 
</div>

</template>

<script>
import db from './firebaseInit.js'
export default {
    name: 'view-employee',
    data() {
        return {
            employee_id: null,
            name: null,
            dept: null,
            position: null,
            type: null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
         .then(querySnapshot => {
             querySnapshot.forEach(doc => {
                 next(vm => {
                     vm.employee_id = doc.data().employee_id,
                     vm.name = doc.data().name,
                     vm.dept = doc.data().dept,
                     vm.position = doc.data().position,
                     vm.type = doc.data().type
                 })
             })
         })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.employee_id = doc.data().employee_id,
                        this.name = doc.data().name,
                        this.dept = doc.data().dept,
                        this.position = doc.data().position,
                        this.type = doc.data().type
                    })
                })
        },
        deleteEmployee() {
            if (confirm('Are you sure you want to delete this employee?')) {
                db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}

</script>

<style scoped>

.inline-icon {
   vertical-align: bottom !important;
   font-size: 30px !important;
   line-height: 35px;
}

</style>


