<template>
    <div>
        <canvas id="typeChart"></canvas>
    </div>
</template>

<script>

export default {
    name: 'employee-type-chart',
    data() {
        return {
            employeeTypeData,
        }
    },
    methods: {
        createChart(chartId, chartData) {
            const ctx = document.getElementById(chartId);
            const myChart = new Chart(ctx, {
            type: chartData.type,
            data: chartData.data,
            options: chartData.options,
            });
        }
    },
    mounted() {
        this.createChart('typeChart', this.employeeTypeData);
    } 
}

const employeeTypeData = {
    type: 'doughnut',
    data: {
      labels: ['Contract', "Full-time"],
      datasets: [
        {
          label: 'Number of Moons',
          data: [6, 2],
        },
      ]
    },
    options: {
      responsive: true,
      lineTension: 1,
      scales: {
        yAxes: [{
          ticks: {
            beginAtZero: true,
            padding: 25,
          }
        }]
      }
    }
  }
</script>


