<template>

<div id="dashboard">
    <!-- <etc></etc> -->
    
    <span><h4>Employees </h4><p><em>({{employeeTotal}} total)</em></p></span>
    <ul class="collection width-header">
        <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
            <div class="chip yellow lighten-2">{{ employee.dept}}</div>
            {{ employee.name }}
            <router-link class="secondary-content"
            v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
            <i class="fas fa-info-circle inline-icon"></i>
            </router-link>
            </li>
    </ul>
    <div class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
            <i class="fa fa-plus"></i>
        </router-link>
    </div>


</div>

</template>

<script>
import db from './firebaseInit.js'
import etc from './EmployeeTypeChart'

export default {
    name: 'dashboard',
    components: {
        etc
    },
    data() {
        return {
            employees: [],
            employeeTotal: '',
            employeeTypes: {
                'full-time': null,
                'contract': null,
            }
        }
    },
    created () {
        db.collection('employees').orderBy('dept').get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                // console.log(doc.data());
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position,
                    'type': doc.data().type
                }
                this.employees.push(data)
            })
            this.employeeTotal = this.employees.length
            this.employeeTypes['full-time'] = this.employees.filter(obj => obj.type === 'full-time').length;
            this.employeeTypes['contract'] = this.employees.filter(obj => obj.type === 'contract').length;
        })
    }
}

</script>

<style scoped>

span {
    display: flex;
}

p {
    color: gray;
    line-height: 45px;
    font-size: 18px;
    vertical-align: bottom !important;
    padding-left: 5px;
}

.inline-icon {
   vertical-align: bottom !important;
   font-size: 25px !important;
   line-height: 35px;
   color: rgb(0, 195, 255);
}

</style>


